@font-face {
  font-family: 'MyYuGothicM';
  font-weight: normal;
  src: local('YuGothic-Medium'),
         /* Postscript name */ local('Yu Gothic Medium'),
         /* for Chrome */ local('YuGothic-Regular'); /* Medium */
}

@font-face {
  font-family: 'MyYuGothicM';
  font-weight: bold;
  src: local('YuGothic-Bold'), /* Postscript name */ local('Yu Gothic'); /* for Chrome */
}

html {
  font-family: 'Noto Sans JP',
    'Noto Sans CJK JP',
    'Noto Sans CJK Japanese',
    MyYuGothicM, /* Windows  */
    YuGothic, /* OS X  */
    -apple-system, /* OS X, iOS San Francisco */
    BlinkMacSystemFont, /* OS X, iOS Chrome San Francisco */
    'Hiragino Kaku Gothic ProN', /* OS X, iOS  */
    Meiryo, /* Windows  */
    sans-serif;
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}